<template>
  <div style="position: relative">
    <section
      class="mainChartBox"
      :style="{ backgroundImage: ' url(' + backgroundImage3 + ')' }"
    >
      <div class="BigContent">
        <div class="content content1">
          <template v-for="(item, key) in sourceData['恒生pb']">
            <div :key="key" style="width: 412px;height: 290px;">
              <header class="chartsTitle">{{ key }}</header>
              <TransChartsBar
                :sourceData="JSON.parse(JSON.stringify(item))"
                bgContentHeight="200px"
                height="60px"
              />
            </div>
          </template>
          <!-- <div style="width: 412px;height: 290px;">
              <div class="chartsTitle">现货分仓前置机</div>
              <TransChartsBar bgContentHeight="200px" height="60px"/>
            </div>
            <div style="width: 412px;height: 290px;">
              <div class="chartsTitle">现货分仓前置机</div>
              <TransChartsBar bgContentHeight="200px" height="60px"/>
            </div>
            <div style="width: 412px;height: 290px;">
              <div class="chartsTitle">现货分仓前置机</div>
              <TransChartsBar bgContentHeight="200px" height="60px"/>
            </div>
            <div style="width: 412px;height: 290px;">
              <div class="chartsTitle">现货分仓前置机</div>
              <TransChartsBar bgContentHeight="200px" height="60px"/>
            </div> -->
        </div>
        <div class="content content2">
          <header class="header">自营数据承载视图</header>
          <template v-for="(item, key) in sourceData['自营数据承载视图']">
            <div
              :key="key"
              style="width: 412px;height: 260px;"
              class="transChartsBar"
            >
              <header class="header">{{ key }}</header>
              <TransChartsBar
                :sourceData="JSON.parse(JSON.stringify(item))"
                bgContentHeight="200px"
                height="60px"
                style="background: rgb(4 105 255 / 0.1);"
              />
            </div>
          </template>
          <!-- <template v-for="key in 7">
              <div :key="key" style="width: 412px" class="transChartsBar">
                <header class="header">深圳业务处理情况</header>
                <TransChartsBar bgContentHeight="200px" height="60px" style="background: rgb(4 105 255 / 0.1);"/>
              </div>
            </template> -->
          <div style="width: 412px"></div>
          <div style="width: 412px"></div>
        </div>
      </div>
    </section>
  </div>
</template>
<script>
import backgroundImage3 from "@/assets/zaiBeiQieHuan/1-3.jpg";
import ProgressBar from "@/components/ProgressBar";
import websockObject from "@/mixins/websock";
import TransChartsBar from "@/components/TransChartsBar";
import { left3Data } from "../../../../static/zaibeiLeftData";
export default {
  name: "Left3",
  mixins: [websockObject],
  data() {
    return {
      backgroundImage3,
      sourceData: {}
    };
  },
  created() {
    console.log(left3Data);
    this.sourceData = {};
    left3Data.forEach(item => {
      this.sourceData[item.group] = {};
      item.data.forEach(elem => {
        this.sourceData[item.group][elem] = {
          凤岗: { value: 0, percValue: 0 },
          后海: { value: 0, percValue: 0 },
          金桥: { value: 0, percValue: 0 }
        };
      });
    });
  },
  mounted() {
    // eslint-disable-next-line no-undef
    this.wsuri = BaseUrl.zaibeiUrl.leftWebsockUrl;
  },
  methods: {
    getWebsocketMsg(sourDate) {
      if (sourDate.length > 0) {
        this.transformtionData(sourDate);
      }
      return new Promise(function(resolve, reject) {
        resolve();
      });
    },
    useTheTestDate() {
      const sourceData = [
        {
          dateTime: 1599623630795,
          value: parseInt(Math.random() * 10000),
          percValue: parseInt(Math.random() * 100),
          title: "恒生pb",
          group: "行情转入情况",
          indicateName: "凤岗"
        },
        {
          dateTime: 1599623630795,
          value: parseInt(Math.random() * 10000),
          percValue: parseInt(Math.random() * 100),
          title: "恒生pb",
          group: "行情转入情况",
          indicateName: "后海"
        },
        {
          dateTime: 1599623630795,
          value: parseInt(Math.random() * 10000),
          percValue: parseInt(Math.random() * 100),
          title: "恒生pb",
          group: "行情转入情况",
          indicateName: "金桥"
        },
        {
          dateTime: 1599623630795,
          value: parseInt(Math.random() * 10000),
          percValue: parseInt(Math.random() * 100),
          title: "恒生pb",
          group: "数据库交易成功",
          indicateName: "凤岗"
        },
        {
          dateTime: 1599623630795,
          value: parseInt(Math.random() * 10000),
          percValue: parseInt(Math.random() * 100),
          title: "恒生pb",
          group: "数据库交易成功",
          indicateName: "后海"
        },
        {
          dateTime: 1599623630795,
          value: parseInt(Math.random() * 10000),
          percValue: parseInt(Math.random() * 100),
          title: "恒生pb",
          group: "数据库交易成功",
          indicateName: "金桥"
        },
        {
          dateTime: 1599623630795,
          value: parseInt(Math.random() * 10000),
          percValue: parseInt(Math.random() * 100),
          title: "自营数据承载视图",
          group: "深圳业务处理",
          indicateName: "凤岗"
        },
        {
          dateTime: 1599623630795,
          value: parseInt(Math.random() * 10000),
          percValue: parseInt(Math.random() * 100),
          title: "自营数据承载视图",
          group: "深圳业务处理",
          indicateName: "后海"
        },
        {
          dateTime: 1599623630795,
          value: parseInt(Math.random() * 10000),
          percValue: parseInt(Math.random() * 100),
          title: "自营数据承载视图",
          group: "深圳业务处理",
          indicateName: "金桥"
        }
      ];
      this.$nextTick(() => {
        this.transformtionData(sourceData);
      });
    },
    transformtionData(data) {
      data.forEach(item => {
        let objData = {
          value: item.value,
          percValue: item.percValue
        };
        if (
          this.sourceData[item.title] &&
          this.sourceData[item.title][item.group] &&
          this.sourceData[item.title][item.group][item.indicateName]
        ) {
          this.$set(
            this.sourceData[item.title][item.group],
            item.indicateName,
            objData
          );
        }
      });
      this.$forceUpdate();
    }
  },
  components: {
    ProgressBar,
    TransChartsBar
  }
};
</script>

<style scoped>
.BigContent {
  top: 0;
  width: 100%;
  position: absolute;
}
.mainChartBox {
  width: 1920px;
  height: 1080px;
  overflow: hidden;
}
.header {
  width: 1750px;
  margin: auto;
  text-align: left;
  font-size: 36px;
  height: 100px;
  line-height: 100px;
}

.chartsTitle {
  font-size: 28px;
  color: #3eabff;
  text-indent: 15px;
  text-align: left;
  margin: 20px 0;
}
.content {
  width: 1738px;
  margin-left: 100px;
}
.content1 {
  height: 315px;
  position: relative;
  overflow: hidden;
  display: flex; /*定义为弹性盒子*/
  justify-content: space-between;
  flex-direction: row;
  flex-wrap: wrap; /*设置溢出是否换行*/
}
.content2 {
  height: 650px;
  margin-top: 30px;
  position: relative;
  overflow: hidden;
  display: flex; /*定义为弹性盒子*/
  justify-content: space-between;
  flex-direction: row;
  flex-wrap: wrap;
}
.transChartsBar {
  margin-bottom: 20px;
  padding-left: 10px;
}
.transChartsBar .header {
  height: 48px;
  font-size: 28px;
  color: #3eabff;
  line-height: 48px;
  text-align: left;
  text-indent: 10px;
}
</style>



