<template>
  <div style="position: relative">
    <section
      class="mainChartBox"
      :style="{ backgroundImage: ' url(' + backgroundImage2 + ')' }"
    >
      <div class="BigContent">
        <div class="content1">
          <div
            style="width: 550px;height: 965px;display: inline-block;float: left;"
          >
            <header class="header mainTitile">量化家</header>
            <template v-for="(item, key) in sourceData['量化家']">
              <div :key="key" style="width: 550px;height: 290px;">
                <header class="chartsTitle">{{ key }}</header>
                <TransChartsBar
                  :sourceData="JSON.parse(JSON.stringify(item))"
                  bgContentHeight="200px"
                  height="60px"
                />
              </div>
            </template>
            <!-- <div style="width: 550px;height: 290px;">
                <div class="chartsTitle">行情转入情况</div>
                <TransChartsBar bgContentHeight="200px" height="60px"/>
              </div>
              <div style="width: 550px;height: 290px;">
                <div class="chartsTitle">现货分仓前置机</div>
                <TransChartsBar bgContentHeight="200px" height="60px"/>
              </div>
              <div style="width: 550px;height: 290px;">
                <div class="chartsTitle">数据库成交情况</div>
                <TransChartsBar bgContentHeight="200px" height="60px"/>
              </div> -->
          </div>
          <div style="width: 550px;height: 965px;display: inline-block">
            <header class="header">迅投pb</header>
            <template v-for="(item, key) in sourceData['迅投pb']">
              <div :key="key" style="width: 550px;height: 290px;">
                <header class="chartsTitle">{{ key }}</header>
                <TransChartsBar
                  :sourceData="JSON.parse(JSON.stringify(item))"
                  bgContentHeight="200px"
                  height="60px"
                />
              </div>
            </template>
            <!-- <div style="width: 550px;height: 290px;">
                <div class="chartsTitle">行情转入情况</div>
                <TransChartsBar bgContentHeight="200px" height="60px"/>
              </div>
              <div style="width: 550px;height: 290px;">
                <div class="chartsTitle">现货分仓前置机</div>
                <TransChartsBar bgContentHeight="200px" height="60px"/>
              </div>
              <div style="width: 550px;height: 290px;">
                <div class="chartsTitle">数据库成交情况</div>
                <TransChartsBar bgContentHeight="200px" height="60px"/>
              </div> -->
          </div>
          <div
            style="width: 550px;height: 965px;display: inline-block;float: right;"
          >
            <header class="header mainTitile">络町pb</header>
            <template v-for="(item, key) in sourceData['络町pb']">
              <div :key="key" style="width: 550px;height: 290px;">
                <header class="chartsTitle">{{ key }}</header>
                <TransChartsBar
                  :sourceData="JSON.parse(JSON.stringify(item))"
                  bgContentHeight="200px"
                  height="60px"
                />
              </div>
            </template>
            <!-- <div style="width: 550px;height: 290px;">
                <div class="chartsTitle">行情转入情况</div>
                <TransChartsBar bgContentHeight="200px" height="60px"/>
              </div>
              <div style="width: 550px;height: 290px;">
                <div class="chartsTitle">现货分仓前置机</div>
                <TransChartsBar bgContentHeight="200px" height="60px"/>
              </div>
              <div style="width: 550px;height: 290px;">
                <div class="chartsTitle">数据库成交情况</div>
                <TransChartsBar bgContentHeight="200px" height="60px"/>
              </div> -->
          </div>
        </div>
        <div class="content2">
          <header class="header mainTitile">恒生pb</header>
        </div>
      </div>
    </section>
  </div>
</template>
<script>
import backgroundImage2 from "@/assets/zaiBeiQieHuan/1-2.jpg";
import ProgressBar from "@/components/ProgressBar";
import TransChartsBar from "@/components/TransChartsBar";
import websockObject from "@/mixins/websock";
import { left2Data } from "../../../../static/zaibeiLeftData";
export default {
  name: "ZBQHLeftHeader",
  mixins: [websockObject],
  data() {
    return {
      backgroundImage2,
      sourceData: {}
    };
  },
  created() {
    this.sourceData = {};
    left2Data.forEach(item => {
      this.sourceData[item.group] = {};
      item.data.forEach(elem => {
        this.sourceData[item.group][elem] = {
          凤岗: { value: 0, percValue: 0 },
          后海: { value: 0, percValue: 0 },
          金桥: { value: 0, percValue: 0 }
        };
      });
    });
  },
  mounted() {
    // eslint-disable-next-line no-undef
    this.wsuri = BaseUrl.zaibeiUrl.leftWebsockUrl;
  },
  methods: {
    getWebsocketMsg(sourDate) {
      if (sourDate.length > 0) {
        this.transformtionData(sourDate);
      }
      return new Promise(function(resolve, reject) {
        resolve();
      });
    },
    useTheTestDate() {
      const sourceData = [
        {
          dateTime: 1599623630795,
          value: parseInt(Math.random() * 10000),
          percValue: parseInt(Math.random() * 100),
          title: "量化家",
          group: "google网页界面获取业务",
          indicateName: "凤岗"
        },
        {
          dateTime: 1599623630795,
          value: parseInt(Math.random() * 10000),
          percValue: parseInt(Math.random() * 100),
          title: "量化家",
          group: "google网页界面获取业务",
          indicateName: "后海"
        },
        {
          dateTime: 1599623630795,
          value: parseInt(Math.random() * 10000),
          percValue: parseInt(Math.random() * 100),
          title: "量化家",
          group: "google网页界面获取业务",
          indicateName: "金桥"
        },
        {
          dateTime: 1599623630795,
          value: parseInt(Math.random() * 10000),
          percValue: parseInt(Math.random() * 100),
          title: "迅投pb",
          group: "数据库交易委托",
          indicateName: "凤岗"
        },
        {
          dateTime: 1599623630795,
          value: parseInt(Math.random() * 10000),
          percValue: parseInt(Math.random() * 100),
          title: "迅投pb",
          group: "数据库交易委托",
          indicateName: "后海"
        },
        {
          dateTime: 1599623630795,
          value: parseInt(Math.random() * 10000),
          percValue: parseInt(Math.random() * 100),
          title: "迅投pb",
          group: "数据库交易委托",
          indicateName: "金桥"
        },
        {
          dateTime: 1599623630795,
          value: parseInt(Math.random() * 10000),
          percValue: parseInt(Math.random() * 100),
          title: "络町pb",
          group: "数据库交易成交",
          indicateName: "凤岗"
        },
        {
          dateTime: 1599623630795,
          value: parseInt(Math.random() * 10000),
          percValue: parseInt(Math.random() * 100),
          title: "络町pb",
          group: "数据库交易成交",
          indicateName: "后海"
        },
        {
          dateTime: 1599623630795,
          value: parseInt(Math.random() * 10000),
          percValue: parseInt(Math.random() * 100),
          title: "络町pb",
          group: "数据库交易成交",
          indicateName: "金桥"
        }
      ];
      this.$nextTick(() => {
        this.transformtionData(sourceData);
      });
    },
    transformtionData(data) {
      data.forEach(item => {
        let objData = {
          value: item.value,
          percValue: item.percValue
        };
        if (
          this.sourceData[item.title] &&
          this.sourceData[item.title][item.group] &&
          this.sourceData[item.title][item.group][item.indicateName]
        ) {
          this.$set(
            this.sourceData[item.title][item.group],
            item.indicateName,
            objData
          );
        }
      });
      this.$forceUpdate();
    }
  },
  components: {
    ProgressBar,
    TransChartsBar
  }
};
</script>

<style scoped>
.mainChartBox {
  width: 1920px;
  height: 1080px;
  overflow: hidden;
}
.BigContent {
  top: 0;
  width: 100%;
  position: absolute;
}
.header {
  font-size: 30px;
  color: #ffffff;
  text-align: left;
  text-indent: 10px;
  margin-top: 10px;
}
.chartsTitle {
  font-size: 28px;
  color: #3eabff;
  text-indent: 10px;
  text-align: left;
  margin: 20px 0;
}
.content1 {
  height: 965px;
  width: 1738px;
  margin-left: 100px;
}
.content2 {
  margin-top: 45px;
  height: 70px;
  width: 1738px;
  margin-left: 100px;
  line-height: 70px;
}
.orderArray {
  position: relative;
  overflow: auto;
  display: flex; /*定义为弹性盒子*/
  justify-content: space-between;
  flex-direction: row;
  flex-wrap: wrap; /*设置溢出是否换行*/
}
</style>



