<template>
  <div style="position: relative">
    <section
      class="mainChartBox"
      :style="{ backgroundImage: ' url(' + backgroundImage1 + ')' }"
    >
      <div class="BigContent">
        <div class="content content1">
          <div style="width: 1750px;margin: auto;">
            <header class="header mainTitile">零售数据承载视图</header>
            <div class="orderArray">
              <template v-for="(item, key) in sourceData['零售数据承载视图']">
                <div :key="key" style="width: 560px" class="transChartsBar">
                  <header class="header">{{ key }}</header>
                  <TransChartsBar
                    :sourceData="JSON.parse(JSON.stringify(item))"
                    bgContentHeight="200px"
                    height="60px"
                    style="background: rgb(4 105 255 / 0.1);"
                  />
                </div>
              </template>
              <div style="width: 560px"></div>
            </div>
          </div>
        </div>
        <div class="content content2">
          <div style="width: 1750px;margin: auto;">
            <header class="header mainTitile">机构数据承载视图</header>
          </div>
        </div>
      </div>
    </section>
  </div>
</template>
<script>
import backgroundImage1 from "@/assets/zaiBeiQieHuan/1-1.jpg";
import ProgressBar from "@/components/ProgressBar";
import TransChartsBar from "@/components/TransChartsBar";
import websockObject from "@/mixins/websock";
import { left1Data } from "../../../../static/zaibeiLeftData";
export default {
  name: "Left1",
  mixins: [websockObject],
  data() {
    return {
      backgroundImage1,
      sourceData: {}
    };
  },
  created() {
    this.sourceData = {};
    left1Data.forEach(item => {
      this.sourceData[item.group] = {};
      item.data.forEach(elem => {
        this.sourceData[item.group][elem] = {
          凤岗: { value: 0, percValue: 0 },
          后海: { value: 0, percValue: 0 },
          金桥: { value: 0, percValue: 0 }
        };
      });
    });
  },
  mounted() {
    // eslint-disable-next-line no-undef
    this.wsuri = BaseUrl.zaibeiUrl.leftWebsockUrl;
  },
  methods: {
    getWebsocketMsg(sourDate) {
      if (sourDate.length > 0) {
        this.transformtionData(sourDate);
      }
      return new Promise(function(resolve, reject) {
        resolve();
      });
    },
    useTheTestDate() {
      const sourceData = [
        {
          dateTime: 1599623630795,
          value: parseInt(Math.random() * 10000),
          percValue: parseInt(Math.random() * 100),
          title: "零售数据承载视图",
          group: "fs网关连接数",
          indicateName: "凤岗"
        },
        {
          dateTime: 1599623630795,
          value: parseInt(Math.random() * 10000),
          percValue: parseInt(Math.random() * 100),
          title: "零售数据承载视图",
          group: "fs网关连接数",
          indicateName: "后海"
        },
        {
          dateTime: 1599623630795,
          value: parseInt(Math.random() * 10000),
          percValue: parseInt(Math.random() * 100),
          title: "零售数据承载视图",
          group: "fs网关连接数",
          indicateName: "金桥"
        },
        {
          dateTime: 1599623630795,
          value: parseInt(Math.random() * 10000),
          percValue: parseInt(Math.random() * 100),
          title: "零售数据承载视图",
          group: "KCXP连接数",
          indicateName: "凤岗"
        },
        {
          dateTime: 1599623630795,
          value: parseInt(Math.random() * 10000),
          percValue: parseInt(Math.random() * 100),
          title: "零售数据承载视图",
          group: "KCBP服务统计",
          indicateName: "凤岗"
        },
        {
          dateTime: 1599623630795,
          value: parseInt(Math.random() * 10000),
          percValue: parseInt(Math.random() * 100),
          title: "零售数据承载视图",
          group: "数据库交易委托",
          indicateName: "凤岗"
        },
        {
          dateTime: 1599623630795,
          value: parseInt(Math.random() * 10000),
          percValue: parseInt(Math.random() * 100),
          title: "零售数据承载视图",
          group: "数据库交易成交",
          indicateName: "凤岗"
        }
      ];
      this.$nextTick(() => {
        this.transformtionData(sourceData);
      });
    },
    transformtionData(data) {
      data.forEach(item => {
        let objData = {
          value: item.value,
          percValue: item.percValue
        };
        if (
          this.sourceData[item.title] &&
          this.sourceData[item.title][item.group] &&
          this.sourceData[item.title][item.group][item.indicateName]
        ) {
          this.$set(
            this.sourceData[item.title][item.group],
            item.indicateName,
            objData
          );
        }
      });
      this.$forceUpdate();
    }
  },
  components: {
    ProgressBar,
    TransChartsBar
  }
};
</script>

<style scoped>
.mainChartBox {
  width: 1920px;
  height: 1080px;
  overflow: hidden;
}
.BigContent {
  top: 0;
  width: 100%;
  position: absolute;
}
.progressnull {
  height: 60px;
  width: 280px;
  margin: 0 10px;
}
.header {
  width: 1750px;
  margin: auto;
  text-align: left;
  font-size: 36px;
  height: 80px;
  line-height: 80px;
}
.headerSmall {
  font-size: 28px;
  text-indent: 36px;
}

.orderArray {
  position: relative;
  overflow: hidden;
  display: flex; /*定义为弹性盒子*/
  justify-content: space-between;
  flex-direction: row;
  flex-wrap: wrap; /*设置溢出是否换行*/
}
.content {
  width: 1834px;
  margin-left: 55px;
  overflow: hidden;
}
.content1 {
  margin-top: 318px;
  height: 650px;
}
.content2 {
  height: 525px;
}
.transChartsBar {
  margin-bottom: 20px;
}
.transChartsBar .header {
  height: 48px;
  font-size: 28px;
  color: #3eabff;
  line-height: 48px;
  text-align: left;
  text-indent: 10px;
}
.transChartsBar .bgContent {
  width: 100%;
  height: 160px;
  background: rgb(4 105 255 / 0.1);
  position: relative;
}
.transChartsBar .playMiddle {
  position: absolute;
  width: 100%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.transChartsBar .contentDataRight {
  width: 90%;
  height: 50px;
  left: 10%;
  border-left: 2px solid #323a9b;
}
.transChartsBar .contentDataRightAbsolu {
  width: 90%;
  height: 20px;
  position: absolute;
  top: 50%;
  transform: translate(0, -50%);
  width: 100%;
}
.transChartsBar .contentDataRightChartDiv {
  max-width: 80%;
  height: 20px;
  text-align: left;
  position: relative;
}
.transChartsBar .contentDataRightChart {
  height: 20px;
  border-radius: 0 10px 10px 0;
  display: inline-block;
}
.transChartsBar .contentDataRightNumber {
  position: absolute;
  top: 50%;
  transform: translate(0, -50%);
  left: 80%;
  margin-left: 10px;
}
.contentData {
  height: 50px;
  width: 100%;
  position: relative;
}
.contentData .contentDataTitle {
  height: 20px;
  display: inline-block;
  width: 10%;
  left: 0;
}

.contentData > div {
  position: absolute;
  top: 50%;
  transform: translate(0, -50%);
}
</style>



